.actionContainer {
  display: flex;
  z-index: 99999;

  margin-left:auto;
  flex-direction: row;

  position: relative;
}

.actionContainer label{
  font-size: 14px;
  width:76px;
  margin-left:4px;
  color: white;
  background-color: #c54c4c;
  border-radius: 16px;
  position: relative;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}


.actionContainer.block {
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px 0;

  margin-left:unset;
}

.actionContainer.block label{
  font-size: 16px;
  width:90px;
  margin: 8px 0;
}


.actionContainer input{
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.isDesktop .actionContainer label:hover{
  opacity:0.7;
}



